// mui
import { useTheme } from '@mui/material/styles';
import {
    Box,
    Button,
    FormControl,
    FormHelperText,
    InputLabel,
    OutlinedInput,
    Stack,
    Typography,
    Switch
} from '@mui/material';

// third party
import * as Yup from 'yup';
import { Formik } from 'formik';

// project 
import useScriptRef from 'hooks/useScriptRef';
import AnimateButton from 'components/extended/AnimateButton';
import PageContainer from 'layouts/PageContainer';
import PageHeading from 'layouts/PageHeading';


const CustomerAdd = () => {
    const theme = useTheme();
    const scriptedRef = useScriptRef();
    // const scriptedRef = useScriptRef();
    // const matchDownSM = useMediaQuery(theme.breakpoints.down('md'));

    return (

        <PageContainer>
            <PageHeading title="Add Customer" back />
            <Formik
                initialValues={{
                    fullname: ' ',
                    email: 'themarscloud@gmail.com',
                    country: '',
                    state: '',
                    address: '',
                    zipcode: '',
                    phone: '',
                    public: false,
                    submit: null
                }}
                validationSchema={Yup.object().shape({
                    fullname: Yup.string().max(255).required('Full Name is required'),
                    email: Yup.string().email('Must be a valid email').max(255).required('Email is required'),
                    country: Yup.string().max(255).required('Country  is required'),
                    state: Yup.string().max(255).required('State/Region is required'),
                    address: Yup.string().max(255).required('Address  is required'),
                    zipcode: Yup.string().max(10)
                })}
                onSubmit={async (values, { setErrors, setStatus, setSubmitting }) => {
                    try {
                        if (scriptedRef.current) {
                            setStatus({ success: true });
                            setSubmitting(false);
                        }
                    } catch (err) {
                        console.error(err);
                        if (scriptedRef.current) {
                            setStatus({ success: false });
                            setErrors({ submit: err.message });
                            setSubmitting(false);
                        }
                    }
                }}
            >
                {({ errors, handleBlur, handleChange, handleSubmit, isSubmitting, touched, values }) => (
                    <form noValidate onSubmit={handleSubmit}>
                        <Stack spacing={3}>
                            <FormControl
                                fullWidth
                                error={Boolean(touched.fullname && errors.fullname)}
                                sx={{ ...theme.typography.customInput }}
                            >
                                <InputLabel htmlFor="outlined-adornment-fullname-customer">Full name</InputLabel>
                                <OutlinedInput
                                    id="outlined-adornment-fullname-customer"
                                    type="input"
                                    value={values.fullname}
                                    name="fullname"
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    label="Full name"
                                    inputProps={{}}
                                />
                                {touched.fullname && errors.fullname && (
                                    <FormHelperText error id="standard-weight-helper-text-fullname-customer">
                                        {errors.fullname}
                                    </FormHelperText>
                                )}
                            </FormControl>
                            <FormControl
                                fullWidth
                                error={Boolean(touched.email && errors.email)}
                                sx={{ ...theme.typography.customInput }}
                            >
                                <InputLabel htmlFor="outlined-adornment-email-login">Email</InputLabel>
                                <OutlinedInput
                                    id="outlined-adornment-email-login"
                                    type="email"
                                    value={values.email}
                                    name="email"
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    label="Email Address / Username"
                                    inputProps={{}}
                                />
                                {touched.email && errors.email && (
                                    <FormHelperText error id="standard-weight-helper-text-email-login">
                                        {errors.email}
                                    </FormHelperText>
                                )}
                            </FormControl>
                            <FormControl
                                fullWidth
                                error={Boolean(touched.country && errors.country)}
                                sx={{ ...theme.typography.customInput }}
                            >
                                <InputLabel htmlFor="outlined-adornment-country-customer">Country</InputLabel>
                                <OutlinedInput
                                    id="outlined-adornment-country-customer"
                                    type="input"
                                    value={values.country}
                                    name="country"
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    label="Country"
                                    inputProps={{}}
                                />
                                {touched.country && errors.country && (
                                    <FormHelperText error id="standard-weight-helper-text-country-customer">
                                        {errors.country}
                                    </FormHelperText>
                                )}
                            </FormControl>
                            <FormControl
                                fullWidth
                                error={Boolean(touched.state && errors.state)}
                                sx={{ ...theme.typography.customInput }}
                            >
                                <InputLabel htmlFor="outlined-adornment-state-customer">State/Region</InputLabel>
                                <OutlinedInput
                                    id="outlined-adornment-state-customer"
                                    type="input"
                                    value={values.state}
                                    name="state"
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    label="State/Region"
                                    inputProps={{}}
                                />
                                {touched.state && errors.state && (
                                    <FormHelperText error id="standard-weight-helper-text-state-customer">
                                        {errors.state}
                                    </FormHelperText>
                                )}
                            </FormControl>
                            <FormControl
                                fullWidth
                                error={Boolean(touched.address && errors.address)}
                                sx={{ ...theme.typography.customInput }}
                            >
                                <InputLabel htmlFor="outlined-adornment-address-customer">Address</InputLabel>
                                <OutlinedInput
                                    id="outlined-adornment-fullname-address"
                                    type="input"
                                    value={values.address}
                                    name="address"
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    label="Address1"
                                    inputProps={{}}
                                />
                                {touched.address && errors.address && (
                                    <FormHelperText error id="standard-weight-helper-text-address-customer">
                                        {errors.address}
                                    </FormHelperText>
                                )}
                            </FormControl>
                            <FormControl
                                fullWidth
                                error={Boolean(touched.zipcode && errors.zipcode)}
                                sx={{ ...theme.typography.customInput }}
                            >
                                <InputLabel htmlFor="outlined-adornment-zipcode-customer">Zip/Code</InputLabel>
                                <OutlinedInput
                                    id="outlined-adornment-zipcode-customer"
                                    type="input"
                                    value={values.zipcode}
                                    name="zipcode"
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    label="Address2"
                                    inputProps={{}}
                                />
                                {touched.zipcode && errors.zipcode && (
                                    <FormHelperText error id="standard-weight-helper-text-zipcode-customer">
                                        {errors.zipcode}
                                    </FormHelperText>
                                )}
                            </FormControl>
                            <FormControl
                                fullWidth
                                error={Boolean(touched.phone && errors.phone)}
                                sx={{ ...theme.typography.customInput }}
                            >
                                <InputLabel htmlFor="outlined-adornment-phone-customer">Phone Number</InputLabel>
                                <OutlinedInput
                                    id="outlined-adornment-phone-customer"
                                    type="input"
                                    value={values.phone}
                                    name="phone"
                                    onBlur={handleBlur}
                                    onChange={handleChange}
                                    label="Phone Number"
                                    inputProps={{}}
                                />
                                {touched.phone && errors.phone && (
                                    <FormHelperText error id="standard-weight-helper-text-phone-customer">
                                        {errors.phone}
                                    </FormHelperText>
                                )}
                            </FormControl>

                            <Typography variant="h4" >
                                Email Verified
                            </Typography>
                            <Stack direction="row" alignItems="center" justifyContent="space-between" spacing={1}>
                                <Typography variant="subtitle1" color="secondary" sx={{ textDecoration: 'none', cursor: 'pointer' }}>
                                    Disabling this will automatically send the user a verification email </Typography>
                                <Switch inputProps={{ 'aria-label': 'Switch demo' }} />
                            </Stack>
                            {/* <Typography variant="h4" >
                                Make Contact Info Public
                            </Typography>
                            <Stack direction="row" alignItems="center" justifyContent="space-between" spacing={1}>
                                <Typography variant="subtitle1" color="secondary" sx={{ textDecoration: 'none', cursor: 'pointer' }}>
                                    Means that anyone viewing your profile will be able to see your contacts details
                                </Typography>
                                <Switch inputProps={{ 'aria-label': 'Switch demo' }} defaultChecked />
                            </Stack> */}

                            {errors.submit && (
                                <Box sx={{ mt: 3 }}>
                                    <FormHelperText error>{errors.submit}</FormHelperText>
                                </Box>
                            )}

                            <Box sx={{ mt: 2 }}>
                                <AnimateButton>
                                    <Button
                                        disableElevation
                                        disabled={isSubmitting}
                                        fullWidth
                                        size="large"
                                        type="submit"
                                        variant="contained"
                                        color="secondary"
                                    >
                                        Create
                                    </Button>
                                </AnimateButton>
                            </Box>
                        </Stack>
                    </form>
                )}
            </Formik>
        </PageContainer>
    );
};

export default CustomerAdd;
